<template>
  <el-form
    :model="mtFormData"
    :rules="rules"
    label-width="100px"
    class="demo-ruleForm mt-5"
    @submit.prevent="console.log(1)"
    action="#"
  >
    <el-form-item label="手机号" :show-message="false" prop="userName">
      <el-input
        @focus="
          alterInfo.userName.info = '';
          alterInfo.userName.isShow = false;
        "
        type="text"
        v-model="mtFormData.userName"
      ></el-input>
      <AlertInfo :alterInfo="alterInfo.userName"></AlertInfo>
    </el-form-item>
    <el-form-item label="短信动态码" :show-message="false" prop="phoneCode">
      <el-input
        @focus="
          alterInfo.phoneCode.info = '';
          alterInfo.phoneCode.isShow = false;
        "
        v-model="mtFormData.phoneCode"
      ></el-input>
      <!-- prop:传进组件内的校验属性 包括 [{ validator: validatePass 校验方式, trigger: "blur" 触发事件类型 }]-->
      <AlertInfo :alterInfo="alterInfo.phoneCode"></AlertInfo>
      <!-- 什么时候这个组件应该出现 文本框被点击一次之后就应该出现 focus不应该出现 -->
    </el-form-item>
    <el-form-item label="创建密码" :show-message="false" prop="password">
      <!-- prop 应该与v-model绑定的属性名相同 -->
      <el-input
        @focus="
          alterInfo.password.info = '';
          alterInfo.password.isShow = false;
        "
        v-model="mtFormData.password"
        type="password"
        @input="input"
      ></el-input>
      <AlertInfo :alterInfo="alterInfo.password"></AlertInfo>
    </el-form-item>
    <ChangeStrength
      :style="{
        'padding-left': '100px',
        'margin-top': '-20px',
        'margin-bottom': '10px',
      }"
      :levelType="levelType"
    />
    <el-form-item label="确认密码" :show-message="false" prop="rePassword">
      <el-input
        @focus="
          alterInfo.rePassword.info = '';
          alterInfo.rePassword.isShow = false;
        "
        v-model="mtFormData.rePassword"
        type="password"
      ></el-input>
      <AlertInfo :alterInfo="alterInfo.rePassword"></AlertInfo>
    </el-form-item>
    <el-form-item class="goup-container">
      <button @click.prevent="handleRegister" class="form-reg-btn">同意以下协议并注册</button>
    </el-form-item>
    <el-form-item class="goup-container">
      <router-link to="/">《美团用户服务协议》</router-link>
      <router-link to="/">《美团隐私政策》</router-link>
    </el-form-item>
  </el-form>
</template>



<script>
import AlertInfo from "./AlertInfo";
import ChangeStrength from "../ChangeStrength";
export default {
  components: {
    AlertInfo,
    ChangeStrength,
  },
  data() {
    return {
      mtFormData: {
        password: "",
        phoneCode: "", // 短信动态码 装个样子
        userName: "",
        rePassword: "",
      },
      alterInfo: {
        password: {
          info: "",
          isShow: false,
        },
        phoneCode: {
          info: "",
          isShow: false,
        }, // 短信动态码 装个样子
        userName: { info: "", isShow: false },
        rePassword: {
          info: "",
          isShow: false,
        },
      },
      levelType: "",
      rules: {
        password: [{ validator: this.validatePass, trigger: "blur" }], // 校验规则和触发事件类型,
        userName: [{ validator: this.validateUsername, trigger: "blur" }], // 校验规则和触发事件类型
        rePassword: [{ validator: this.validateRePass, trigger: "blur" }], // 校验规则和触发事件类型
        phoneCode: [{ validator: this.validateCode, trigger: "blur" }], // 校验规则和触发事件类型
      },
    };
  },
  methods: {
    validatePass(rule, value) {
      rule;
      var reg = /[\dA-Za-z_]*/;
      if (value === "") {
        this.alterInfo.password.info = "不能为空";
      } else if (value.length < 6 || value.length > 16) {
        this.alterInfo.password.info = "密码必须为6-16位的字母数字下划线组成";
      } else {
        if (reg.test(value)) {
          this.alterInfo.password.info = "";
        } else {
          this.alterInfo.password.info = "密码必须为6-16位的字母数字下划线组成";
        }
      }
      this.alterInfo.password.isShow = true;
      //   cb(this.alterInfo.password && new Error(this.alterInfo.password));
    },
    validateRePass(rule, value) {
      rule;
      if (value === "") {
        this.alterInfo.rePassword.info = "不能为空";
      } else if (value !== this.mtFormData.password) {
        this.alterInfo.rePassword.info = "两次输入的密码不一致";
      }
      this.alterInfo.password.info !== "" &&
        (this.alterInfo.rePassword.info = this.alterInfo.password.info);

      this.alterInfo.rePassword.isShow = true;
      //   cb(this.alterInfo.rePassword && new Error(this.alterInfo.rePassword));
    },
    validateUsername(rule, value) {
      rule;
      var reg = /[\dA-Za-z_]*/;
      if (value === "") {
        this.alterInfo.userName.info = "不能为空";
      } else if (value.length < 6 || value.length > 16) {
        this.alterInfo.userName.info = "用户名必须为4-16位的字母数字下划线组成";
      } else {
        if (reg.test(value)) {
          this.alterInfo.userName.info = "";
        } else {
          this.alterInfo.userName.info =
            "用户名必须为4-16位的字母数字下划线组成";
        }
      }
      this.alterInfo.userName.isShow = true;
      //   cb(this.alterInfo.userName && new Error(this.alterInfo.userName));
    },
    validateCode() {},
    input() {
      var reg = /[\dA-Za-z]{6,16}/g;
      if (reg.test(this.mtFormData.password)) {
        this.levelType = "ruo";
        if (this.mtFormData.password.length >= 10) {
          this.levelType = "zhong";
        }
        if (this.mtFormData.password.length > 15) {
          this.levelType = "qiang";
        }
      } else {
        this.levelType = '';
      }
      // 符合密码 并且长度 6 - 9  10 - 13  > 13
    },
    async handleRegister() {
      var res = await this.$api.postRegister(this.mtFormData);
      if(res.status === 'success') {
        alert(res.msg);
        this.$router.push({
          name : "Login",
          param : {
            userName : this.mtFormData.userName
          }
        })
      } else {
        alert(res.msg)
      }
    }
  },
};
</script>



<style lang="less" scoped>
.el-input {
  display: inline-block;
  width: auto;
  /deep/ input {
    width: 248px;
    height: 24px;
    padding: 5px;
    border: 1px solid #aaa;
    line-height: 24px;
    box-sizing: content-box;
    border-radius: 0;
    &:focus {
      border-color: #ffc300;
    }
  }
}
.form-reg-btn {
  color: #222;
  background-image: linear-gradient(135deg, #ffd000 0, #ffbd00 100%);
  border-width: 0;
  box-shadow: 0 2px 1px rgba(191, 105, 0, 0.15);
  font-size: 14px;
  font-weight: 700;
  line-height: 1.5;
  letter-spacing: 0.1em;
  text-align: center;
  border-radius: 2px;
  cursor: pointer;
  box-sizing: content-box;
  padding: 7px 20px 6px;
}
.goup-container {
  margin-top: -20px;
}
a {
  color: #fe8c00;
  font-size: 13px;
}
</style>