<template>
  <div class="position-relative">
    <div class="field origin">
      <span>弱</span><span>中</span><span>强</span>
    </div>
    <div class="field  over position-absolute" :class="levelType">
      <span>弱</span><span>中</span><span>强</span>
    </div>
  </div>
</template>

<script>
export default {
    props : {
        levelType :String 
    }
};
</script>

<style lang="less" scoped>
.field {
  span {
    display: inline-block;
    text-align: center;
    font-size: 12px;
    height: 20px;
    line-height: 20px;
    width: 85px;
    margin-right: 2px;
    color: #fff;
    background: #eeeeee;
  }
  &.over {
    left: 100px;
    top: 0;
    width: 0px;
    overflow: hidden;
    display: inline-flex;
    transition: width 1s;
    span {
      min-width: 85px;
      background: #f76120;
    }
    &.ruo {
      width: calc(1 * 87px);
    }
    &.zhong {
      width: calc(2 * 87px);
